import React, { useContext, useEffect, useState } from 'react'
import { CartContext } from '../../Context/CartContext'
import { RotatingLines } from 'react-loader-spinner';
import { Link } from 'react-router-dom';


export default function Cart() {
  let {getCartItems , deleteCartItems , UpdateCartItems} = useContext(CartContext);
  const [cart , setCart] = useState(null);
  const [loading , setLoading] = useState(true);
  

  async function getItems(){
    let {data} = await getCartItems();
    setCart(data);
    setLoading(false)
  }
  async function deleteItem(id){
    setLoading(true)
    let {data} = await deleteCartItems(id);
    setCart(data);
    setLoading(false)
  }
  async function UpdateCart(id , count){
    // setLoading(true)
if ( count < 1){
  let {data} = await deleteCartItems(id);
  setCart(data);
}else{
  let {data} = await UpdateCartItems(id , count);
  setCart(data);
  setLoading(false)
}
  }

  useEffect(()=>{
    getItems()
  } , [])
  return<>
  <div className="bg-main-light p-2 mt-5 ">
    <h2 className='ms-3 py-3'>Shop Cart:</h2>
  {loading? <div className="loading">
  <RotatingLines
  visible={true}
  height="96"
  width="96"
  color="grey"
  strokeWidth="5"
  animationDuration="0.75"
  ariaLabel="rotating-lines-loading"
  wrapperStyle={{}}
  wrapperClass="d-flex justify-content mt-5 align-items-center"
  />
  </div> :cart? <>
  <p className='text-main ms-3'>total number of items:{cart.numOfCartItems}</p>
  <p className='text-main ms-3'>total price:{cart.data.totalCartPrice} EGP</p>
  {cart.data.products.map((product , index) => 
   <div key={index} className='row border-2 border-bottom p-2 m-0 align-items-center'>
   <div className='col-md-2'>
   <div className="img">
    <img src={product.product.imageCover} className='w-100 ms-0' alt={product.product.title} />
   </div>
   </div>
   <div className="col-md-9">
    <div className="item">
      <h3 className='h5 fw-bold'>{product.product.title.split(' ').slice(0,3).join(' ')}</h3>
      <p className='text-main fw-bold'>{product.price} EGP</p>
      <button onClick={()=> deleteItem(product.product.id)} className='btn'> <i className='fas fa-trash-can text-danger'></i> Remove</button>
    </div>
   </div>
   <div className="col-md-1">
    <div className="count">
      <button onClick={()=> UpdateCart(product.product.id , product.count +1)} className='btn brdr py-0 px-1'>+</button>
      <span className='mx-2'>{product.count}</span>
      <button onClick={()=> UpdateCart(product.product.id , product.count -1)} className='btn brdr py-0 px-1'>-</button>
    </div>
   </div>
   </div> )}

   <Link to={`/shippingaddress/${cart.data._id}`} className='btn text-bg-primary text-light m-3' >check out</Link>
  </> : <h2>Cart empty...</h2> }
  </div>
  </>
  
}
