import { useFormik } from 'formik';
import React from 'react'
import { useContext } from 'react';
import { CartContext } from '../../Context/CartContext';
import { useParams } from 'react-router-dom';

export default function ShippingAddress() {
  let {checkOutUser} = useContext(CartContext)
  let {cartId} = useParams()

async function checkOut(values){
  let {data} = await checkOutUser( cartId , values );
  console.log(data);
  if (data.status == 'success'){
    window.location.href = data.session.url
  }
}

let formik = useFormik({
  initialValues:{
    details:'',
    phone:'',
    city:''
  }, onSubmit: checkOut
})


  return<>
  <div className="w-100 mx-auto">
    <form onSubmit={formik.handleSubmit} className='py-5'>
      <label htmlFor="details">Details</label>
      <input onChange={formik.handleChange} type="text" id='details' name='details' className='form-control mb-3' />
      <label htmlFor="phone">Phone</label>
      <input onChange={formik.handleChange} type="tel" id='phone' name='phone' className='form-control mb-3' />
      <label htmlFor="city">City</label>
      <input onChange={formik.handleChange} type="text" id='city' name='city' className='form-control mb-3' />
      <button className='btn btn-outline-info w-100 my-5' type='submit'>Pay now</button>
    </form>
  </div>
  </>
}
