import React, { useEffect } from 'react'
import { getBrands } from '../../Redux/BrandsSlice'
import { useDispatch, useSelector } from 'react-redux'
import { RotatingLines } from 'react-loader-spinner'

export default function Brands() {
 let {brands , isLoading} = useSelector(({brand}) => brand )

let dispatch =useDispatch()

useEffect(()=>{
  dispatch(getBrands())
} , [])

  return<>
    {isLoading? <div className="loading">
  <RotatingLines
  visible={true}
  height="96"
  width="96"
  color="grey"
  strokeWidth="5"
  animationDuration="0.75"
  ariaLabel="rotating-lines-loading"
  wrapperStyle={{}}
  wrapperClass="d-flex justify-content mt-5 align-items-center"
  />
  </div> : <div className="row py-5">
   {brands.map(brand => 
    <div key={brand._id} className="col-md-3">
    <div className="product">
      <img src={brand.image} className='w-100' alt={brand.name} />
      <p className='d-flex justify-content justify-content-center align-items-center'>{brand.name}</p>
    </div>
    </div> )}
  </div> }
  </>
}
