import axios from 'axios'
import { useFormik } from 'formik'
import React, { useContext, useState } from 'react'
import { RotatingLines } from 'react-loader-spinner'
import { Link , useNavigate } from 'react-router-dom'
import * as Yup from 'yup'
import { UserContext } from '../../Context/UserContext'


export default function Login() {
  const [loading, setLoading] = useState(false)
  const [apiError, setApiError] = useState(null)
  let navigate = useNavigate()
  let {setUserToken} = useContext(UserContext)

async function loginSubmit(values){
  setLoading(true)
  let {data} = await axios.post(`https://ecommerce.routemisr.com/api/v1/auth/signin` , values)
  .catch((err)=>{
    setApiError(err.response.data.message)
    setLoading(false)
  })
  if (data.message == 'success'){
    setLoading(false)
    localStorage.setItem('userToken' , data.token)
    setUserToken(data.token)
    navigate('/')
  }
  
}
let validationSchema = Yup.object({

  email : Yup.string().required('email is required').email('invalid email'),
  password : Yup.string().required('password is required').matches(/^[A-Z][\w @]{5,8}$/ , 'invalid password ex(Gehad123)')

})

 let formik = useFormik({
  initialValues:{
    email:'',
    password:'',

  }, validationSchema
  ,onSubmit:loginSubmit
 })

  return<>
  <div className="w-75 mx-auto py-4">
    <h2>Login Now</h2>
    <form onSubmit={formik.handleSubmit}>
{apiError?<div className='alert alert-danger'>{apiError}</div>:''}

     <label htmlFor="email">Email :</label>
     <input onBlur={formik.handleBlur} onChange={formik.handleChange} type="email" name="email" id="email" className='form-control mb-3' />
     {formik.errors.email && formik.touched.email ? <div className='alert alert-danger py-2'>{formik.errors.email}</div> :null}

     <label htmlFor="password">Password :</label>
     <input onBlur={formik.handleBlur} onChange={formik.handleChange} type="password" name="password" id="password" className='form-control mb-3' />
     {formik.errors.password && formik.touched.password ? <div className='alert alert-danger py-2'>{formik.errors.password}</div> :null}

     {loading?    <button  type='butten' className='btn bg-main text-light'>
     <RotatingLines
  visible={true}
  height={25}
  width={25}
  color="#fff"
  strokeWidth="5"
  animationDuration="0.75"
  ariaLabel="rotating-lines-loading"
  wrapperStyle={{}}
  wrapperClass=""
  />
    </button>: <button disabled={!(formik.isValid && formik.dirty)} type='submit' className='btn bg-main text-light'>Login</button>}
    <Link className='ps-3' to={'/register'}>Register Now</Link>
    </form>
  </div>
  </>
}
