import React, { useEffect } from 'react'
import { getCategories } from '../../Redux/CategoriesSlice'
import { useDispatch, useSelector } from 'react-redux'
import { RotatingLines } from 'react-loader-spinner'

export default function Categories() {
 let {categories , isLoading} = useSelector(({category}) => category )

let dispatch =useDispatch()

useEffect(()=>{
  dispatch(getCategories())
} , [])

  return<>
    {isLoading? <div className="loading">
  <RotatingLines
  visible={true}
  height="96"
  width="96"
  color="grey"
  strokeWidth="5"
  animationDuration="0.75"
  ariaLabel="rotating-lines-loading"
  wrapperStyle={{}}
  wrapperClass="d-flex justify-content mt-5 align-items-center"
  />
  </div> : <div className="row py-5">
   {categories.map(category => 
    <div key={category._id} className="col-md-4">
    <div className="product p-3 border rounded-3">
      <img src={category.image} height={400} className='w-100' alt={category.name} />
      <p className='h3 text-main fw-bold align-items-center d-flex justify-content-center py-3 '>{category.name}</p>
    </div>
    </div> )}
  </div> }
  </>
}
