import React, { useContext, useEffect, useState } from 'react'
import { WishListContext } from '../../Context/WishListContext'
import { RotatingLines } from 'react-loader-spinner';


export default function WishList() {
  let {getWishListItems , deleteWishListItems} = useContext(WishListContext);
  const [wishList , setWishList] = useState(null);
  const [loading , setLoading] = useState(true);
  

  async function getItems(){
    let {data} = await getWishListItems();
    setWishList(data);
    setLoading(false)
  }
  async function deleteItem(id){
    setLoading(true)
    let {data} = await deleteWishListItems(id);
    setWishList(data);
    setLoading(false)
  }

  useEffect(()=>{
    getItems()
  } , [])
  return<>
  <div className="bg-main-light p-2 mt-5 ">
    <h2 className='ms-3 py-3'>My wish List:</h2>
  {loading? <div className="loading">
  <RotatingLines
  visible={true}
  height="96"
  width="96"
  color="grey"
  strokeWidth="5"
  animationDuration="0.75"
  ariaLabel="rotating-lines-loading"
  wrapperStyle={{}}
  wrapperClass="d-flex justify-content mt-5 align-items-center"
  />
  </div> : <>
  {wishList.data.map(product => 
   <div key={product.id} className='row border-2 border-bottom p-2 m-0 align-items-center'>
   <div className='col-md-2'>
   <div className="img">
    <img src={product.imageCover} className='w-100 ms-0' alt={product.title} />
   </div>
   </div>
   <div className="col-md-9">
    <div className="item">
      <h3 className='h5 fw-bold'>{product.title.split(' ').slice(0,2).join(' ')}</h3>
      <p className='text-main fw-bold'>{product.price} EGP</p>
      <button onClick={()=> deleteItem(product.id)} className='btn'> <i className='fas fa-trash-can text-danger'></i> Remove</button>
    </div>
   </div>
   <div className="col-md-1">
   </div>
   </div> )}
  </>}
  </div>
  </>
  
}
