import axios from 'axios'
import React, { useEffect, useState } from 'react'
import { RotatingLines } from 'react-loader-spinner';
import { useParams } from 'react-router-dom'
import Slider from "react-slick";

export default function ProductDetails() {
  var settings = {
    dots: false,
    autoplay: true,
    autoplaySpeed:1500,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1
  };

  const [details, setDetails] = useState({});
  const [loading, setLoading] = useState(true);

let {id} = useParams()

async function getProductDetails(id) {

  let {data} = await axios.get(`https://ecommerce.routemisr.com/api/v1/products/${id}`)
  setDetails(data.data)
  setLoading(false)
}

useEffect(()=>{
  getProductDetails(id)
}, [])

  return<>
  {loading?
<div className=" loading ">
<RotatingLines
  visible={true}
  height="96"
  width="96"
  color="grey"
  strokeWidth="5"
  animationDuration="0.75"
  ariaLabel="rotating-lines-loading"
  wrapperStyle={{}}
  wrapperClass="d-flex justify-content mt-5 align-items-center"
  />
</div>:
<div className="row align-items-center">
  <div className="col-md-4">
  <Slider {...settings}>
     {details.images.map((image , index)=> <img src={image} key={index} className='w-100' alt={details.title} /> )}
  </Slider>
  </div>
  <div className="col-md-8">
    <div className="details">

    <h3 className='h5'>{details.title}</h3>
    <p className='py-3'>{details.description}</p>
    <span className='font-sm text-main'>{details.category.name}</span>
    <div className="d-flex justify-content-between align-items-center py-3">

      <span className='font-sm'>{details.price} EGP</span>
      <div >
      <span className='font-sm'>
        <i className='fas fa-star rating-color me-1'></i>
        {details.ratingsAverage}</span>
        <span>
        <i className="fa-solid fa-heart ms-2 text-danger"></i>
        </span>
      </div>
    </div>
    <button className='btn bg-main text-main-light w-100 btn-sm'>Add</button>
    </div>
  </div>
</div>
}
  </>
}
