import React, { useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom'
import logo from '../../Assets/images/freshcart-logo.svg'
import { CounterContext } from '../../Context/CounterContext'
import { UserContext } from '../../Context/UserContext'
import { useSelector } from 'react-redux'
export default function NavBar() {
   let {count} = useSelector(({counter})=> counter)
   let {userToken , setUserToken} = useContext(UserContext)
   let navigate = useNavigate()
   function logout() {
    localStorage.removeItem('userToken');
    setUserToken(null);
    navigate('/login')
   }

  return<>
   <nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
    <Link className="navbar-brand" to={'/'}>
      <img src={logo} alt="fresh cart" />
    </Link>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
      </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className='navbar-nav me-auto mb-2 mb-lg-0'>

        {userToken != null? <>
          <li className="nav-item">
          <Link className="nav-link" to={'/'}>Home</Link>
        </li>

        <li className="nav-item">
          <Link className="nav-link" to={'product'}>Product</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to={'categories'}>Categories</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to={'brands'}>Brands</Link>
          </li>

          <li className="nav-item">
          <Link className="nav-link" to={'cart'}><i class="fa-solid fa-cart-shopping me-2 fs-4 "></i></Link>
        </li>
        
        <li className="nav-item">
          <Link className="nav-link" to={'wishList'}><i class="fa-solid fa-heart me-2 fs-4 "></i></Link>
          </li>

        </>: ''}

      </ul>
    <ul className='navbar-nav ms-auto mb-2 mb-lg-0'>
    <li className="nav-item d-flex align-items-center ">
    <Link className='fab fa-facebook me-2' to={'https://www.facebook.com/'}></Link>
    <Link className='fab fa-twitter me-2' to={'https://www.twitter.com/'}></Link>
    <Link className='fab fa-instagram me-2' to={'https://www.instagram.com/'}></Link>
    <Link className='fab fa-youtube me-2' to={'https://www.youtube.com/'}></Link>
        </li>

        {userToken != null? <>
          <li className="nav-item">
          <span onClick={logout} className="nav-link cursor-pointer" ><i class="fa-solid fa-arrow-right-from-bracket"></i></span>
          </li>
        </>: <>
        <li className="nav-item">
          <Link className="nav-link" to={'register'}>Register</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to={'login'}>Login</Link>
        </li>
        </>}


      </ul>
    </div>
  </div>
</nav>
  </>
}

