import React, { useContext, useEffect, useState } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom';
import { useQuery } from 'react-query';
import { CartContext } from '../../Context/CartContext';
import toast from 'react-hot-toast';
import {RotatingLines } from 'react-loader-spinner';
import { WishListContext } from '../../Context/WishListContext';



export default function FeaturedProducts() {
// const [products, setProducts] = useState([])
// const [loading, setLoading] = useState(true)

// async function getProduct(){
//   let {data} = await axios(`https://ecommerce.routemisr.com/api/v1/products`);
//   setProducts(data.data)
//   setLoading(false)
// }
// useEffect(()=>{
//   getProduct()
// }, [])

function getProducts() {
  return axios.get(`https://ecommerce.routemisr.com/api/v1/products`)
}

let {data , isLoading , isError , isFetched} = useQuery('featuredProducts' , getProducts , {
  refetchInterval:3000000
});
let {addToCart} = useContext(CartContext)
let {addToWishList} = useContext(WishListContext)

async function postToCart(id){
  let {data} = await addToCart(id);
  if (data.status == 'success'){
    toast.success(data.message , {
      duration:5000
    })
  }
}
async function postToWishList(id){
  let {data} = await addToWishList(id);
  if (data.status == 'success'){
    toast.success(data.message , {
      duration:5000
    })
  }
}
  return<>
{isLoading?
<div className=" loading">
<RotatingLines
  visible={true}
  height="96"
  width="96"
  color="grey"
  strokeWidth="5"
  animationDuration="0.75"
  ariaLabel="rotating-lines-loading"
  wrapperStyle={{}}
  wrapperClass="d-flex justify-content mt-5 align-items-center"
  />
</div>

: <div className="row gy-4">
{data?.data.data.map(product =>

<div key={product.id} className="col-md-3 ">

  <div className="product p-2">
  <Link to={`/productdetails/${product.id}`}>
    <img src={product.imageCover} className='w-100' alt={product.title} />
    <span className='font-sm text-main'>{product.category.name}</span>
    <h3 className='h5'>{product.title.split(' ').splice(0,2).join(' ')}</h3>
    <div className="d-flex justify-content-between align-items-center py-3">
      <span className='font-sm'>{product.price} EGP</span>
      <div className="">
      <span className='font-sm'>
        <i className='fas fa-star rating-color me-1'></i>
        {product.ratingsAverage}</span>

      </div>
    </div>
    </Link>
    <button onClick={()=> postToCart(product.id)} className='btn bg-main text-main-light w-100 btn-sm'>Add</button>
  
        <Link  onClick={()=> postToWishList(product.id)} className="fa-solid fa-heart ms-2 text-danger"></Link>
    
  </div>

  </div>

)}

</div> }


  </>
}
